import { InputManager } from '../src/inputManager/InputManager'
import { AnimationFrameTicker } from '../src/ticker/AnimationFrameTicker'

import { DrawingEntity } from './DrawingEntity'

const canvas = document.querySelector<HTMLCanvasElement>('#app')
const ctx = canvas.getContext('2d')

canvas.width = 1000
canvas.height = 500

const inputManager = new InputManager()
const ticker = new AnimationFrameTicker()

inputManager.mount(canvas)

const entity = new DrawingEntity()

ticker.listen(tickerState => {
  const inputState = inputManager.getState()

  ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height)

  entity._update({ tickerState, inputState })

  entity._draw({ tickerState, ctx })

  inputManager.processTick()
})

ticker.start()
